<script>
    import { onMount } from 'svelte';
    import PitchDrillCheck from './PitchDrillCheck.svelte';
    import { toggling, allUnlocks } from '../../store.js';

    export let sentence;
    let allCorrect = false;
    let showAnswer = false;
    let states = [];
    let locks = [];

    const next = {
        'L': 'H',
        'H': 'L',
    };

    $: if (sentence.pitch && !states.length) reset();
    $: if (sentence.pitch && !locks.length) unlock();

    $: if (states.join('') == sentence.pitch) {
        showAnswer = allCorrect = true;
    } else {
        allCorrect = false;
    }

    const change = idx => {
        // console.log(locks[idx])
        if ($toggling && !locks[idx] && sentence && sentence.pitch.charAt(idx) != '_') {
            states[idx] = next[states[idx]];
            locks[idx] = true;
            console.log(states.join(''));
        }
    };

    const reset = () => {
        states = sentence.pitch
            .split('')
            .map(x => x == '_' ? '_' : 'L');
        showAnswer = false;
    };

    const toggleCheck = () => {
        showAnswer = !showAnswer
    }

    const unlock = () => {
        locks = sentence.pitch
            .split('')
            .map(x => false);
    }

    const onTouchmove = (e) => {
        let { clientX, clientY } = e.touches[0];
        let el = document.elementFromPoint(clientX, clientY);
        console.log(el.classList.contains('character'))
        if (el.classList.contains('character')) {
            let clickEvent = document.createEvent('MouseEvents');
            clickEvent.initEvent('mousedown', true, true);
            el.dispatchEvent(clickEvent);
        };
    }

    onMount(() => {
        $allUnlocks.add(unlock);
        return () => allUnlocks.delete(unlock);
    });

</script>

<div class="pitch-accent d-flex flex-row flex-wrap flat">
    {#if sentence.is_sentence}
        <PitchDrillCheck {allCorrect} {toggleCheck} />
    {/if}
    {#each [...sentence.hiragana] as char, idx (idx) }
        <span 
            class="state-{states[idx]}"
            class:character={states[idx] != 2}
            class:correct={showAnswer && states[idx] == sentence.pitch.charAt(idx)}
            class:wrong={showAnswer && states[idx] != sentence.pitch.charAt(idx)}
            class:vertical={ 
                idx > 0 && states[idx] != '_' && states[idx-1] != '_' 
                && states[idx-1] != states[idx]
            }
            class:vertical-wrong={ 
                idx > 0 && states[idx] != '_' && states[idx-1] != '_' 
                && showAnswer && states[idx-1] != sentence.pitch.charAt(idx-1)
            }
            on:mousedown={() => {$toggling = true; change(idx)}}
            on:mouseenter={() => change(idx)}
            on:touchstart={() => {$toggling = true}}
            on:touchmove={onTouchmove}
        >{char}</span>
    {/each}
    {#if sentence.is_sentence}
        <span class="reset-button" on:click={reset}> ♻️ </span> 
    {/if}
</div>

<style>
    .flat {
        display: inline !important;
    }
    .pitch-accent {
        font-size: 28px;
        user-select: none;
        -webkit-user-select: none;
    }
    .character {
        border-color: #f6c143 !important;
        border-width: 4px !important;
        cursor: pointer;
        line-height: 48px;
    }
    .character:hover {
        background-color: #ffe143;
    }
    .correct {
        border-color: limegreen !important;
    }
    .wrong {
        border-color: red !important;
    }
    .vertical {
        border-left: solid;
    }
    .vertical-wrong {
        border-left-color: red !important;;
    }
    .state-L {
        border-bottom: solid;
    }
    .state-H {
        border-top: solid;
    }
    .reset {
        color: gray;
    }
    .reset:hover {
        color: #f6c143;
    }
    .reset-button {
        background-image: url("/assets/images/reset-off.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        color: transparent;
        cursor: pointer;
        display: inline;
        margin-right: 0px;
        user-select: none;
    }
    .reset-button:hover {
        background-image: url("/assets/images/reset-on.png");
    }
</style>