<script>
    import PitchWorkshop from './PitchWorkshop.svelte';
    import { toggling, allUnlocks } from '../store.js';

    const release = () => {
        $toggling = false;
        setTimeout(function() {
            $allUnlocks.forEach(
                unlock => unlock()
            )}, 
        300)
    }
</script>

<svelte:head>
    <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-1BmE4kWBq78iYhFldvKuhfTAU6auU8tT94WrHftjDbrCEXSU1oBoqyl2QvZ6jIW3" crossorigin="anonymous">
    <title>Summer's Japanese Pitch-Accent Workshop</title>
</svelte:head>

<svelte:window 
    on:mouseup={release}
    on:touchend={release}
    on:touchcancel={release}
/>

<PitchWorkshop/>
