<script>
    import { onMount } from 'svelte';
    import PitchDrill from '../Drills/PitchDrill.svelte';
    import { allAudios } from '../../store.js';

    export let sentence = {}; 
    export let idx;
    let player;

    onMount(() => {
        $allAudios.add(player);
        return () => allAudios.delete(player);
    });

    const play = () => {
        $allAudios.forEach(audio => {
            audio.pause();
        });
        player.currentTime = 0;
        player.play();
    }
</script>

<div class="card" class:card--odd={idx % 2 == 1}>
    <audio
        bind:this={player}
        controlsList="nodownload"
        preload="auto"
        src={sentence.audio}
    />
    <div class="text">
        <div class="play-button" on:click={play}>▶</div>
        {sentence.text}
    </div>
    <PitchDrill {sentence}/>
</div>

<style>
    .card {
        background-color: #fdf2d0;
        border: none;
        border-left: solid;
        border-left-color: #eab38a;
        border-left-width: 10px;
        border-radius: 0 15px 15px 0;
        color: #d05f29;
        font-weight: 600;
        font-size: 28px;
        margin-right: .8em;
        margin-bottom: .6em;
        padding: 16px 22px 20px 12px;
    }
    .play-button {
        background-image: url("/assets/images/play-off.png");
        background-position: center;
        background-repeat: no-repeat;
        background-size: contain;
        color: transparent;
        cursor: pointer;
        display: inline;
        margin-right: 0px;
        user-select: none;
    }
    .card--odd {
        border-left-color: #a4c3e3;
    }
    .play-button:hover {
        background-image: url("/assets/images/play-on.png");
    }
    .text {
        color: #999;
        margin-right: 10px;
        margin-bottom: 5px;
    }
</style>